import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {AppRootStoreType} from "../../bll/store";
import {Chart} from "./Chart/Chart";
import {Modal} from "../Modal/Modal";
import {useNavigate} from "react-router-dom";
import {ItemsType} from "../../api/api";
import "../../styles/styles.scss";
import {setCurrencyTC} from "../../bll/setReducer";

export const DetailInformation = () => {

    const dispatch = useDispatch()
    let navigate = useNavigate()
    const [isOpen, setIsOpen] = useState(false)
    const detailsInformation = useSelector<AppRootStoreType, ItemsType>(state => state.setDetailsReducer.detailsInformation)

    const toFloor = (num: string) => {
        if (num) return Math.floor(Number(num))
    }

    const clickHandler = () => {
        dispatch(setCurrencyTC(0))
        navigate("/")
    }

    return (
        <div className={'wrapper-center detail '}>
            <div className={'content-detail'}>
                <h3 className={'title'}>Detail Information</h3>
                <div className={'content-detail__item'}>: {detailsInformation.symbol}</div>
                <div className={'content-detail__item'}> : {detailsInformation.name}</div>
                <div className={'content-detail__item'}> : {toFloor(detailsInformation.supply)}</div>
                <div className={'content-detail__item'}> : {toFloor(detailsInformation.maxSupply)}</div>
                <div className={'content-detail__item'}>: {toFloor(detailsInformation.marketCapUsd)}</div>
                <div className={'content-detail__item'}>   24 : {toFloor(detailsInformation.volumeUsd24Hr)}$</div>
                <div className={'content-detail__item'}>   : {toFloor(detailsInformation.priceUsd)}$</div>
                <div className={'content-detail__item'}>    24 : {toFloor(detailsInformation.changePercent24Hr)}</div>
                <div className={'content-detail__item'}>  24 : {toFloor(detailsInformation.vwap24Hr)}</div>
                <div>
                    <Modal open={isOpen}
                           onClose={() => setIsOpen(false)}
                           id={detailsInformation.id}
                           name={detailsInformation.name}
                           price={detailsInformation.priceUsd}
                    >
                    </Modal>
                </div>
                <div className={'detail-information'}>
                    <button className={'button--inner'} onClick={() => setIsOpen(true)}></button>
                    <button className={'button--inner'} onClick={clickHandler}></button>
                </div>
            </div>
            <div className={'detail__chart detail'}>
                <Chart symbol={detailsInformation.name}/>
            </div>
        </div>
    )
}